          </div>

          <ul>
            <li>
              <img src="../assets/img/chat_icon.png" alt="" style="display: none">
              <img src="../assets/img/chat_icon_.png" alt="" style="display: block">
            </li>
            <li>
              <img src="../assets/img/user_icon.png" alt="">
              <img src="../assets/img/user_icon_.png" alt="">
            </li>
            <li>
              <img src="../assets/img/box_icon.png" alt="">
              <img src="../assets/img/box_icon_.png" alt="">
            </li>
